import { FilterEntity } from "../FilterEntity";
export class PackSearchEntity extends FilterEntity {
    Id: number;
    ProductId: number;
    Code: string;
    StartDate: Date;
    EndDate: Date;

    constructor(Entity: any = null) {
        super(Entity);
        this.Id = Entity == null ? null : Entity.Id;
        this.ProductId = Entity == null ? null : Entity.ProductId;
        this.Code = Entity == null ? null : Entity.Code;
        this.StartDate = Entity == null ? null : Entity.StartDate;
        this.EndDate = Entity == null ? null : Entity.EndDate;
    }
}



